<template>
    <div class="ideal-vacation">
      <div class="form-title">Tell us about your ideal vacation:</div>
      <el-form ref="vacationForm" :model="vacationForm">
        <el-form-item>
          <el-input
            type="textarea"
            v-model="vacationForm.idealVacation"
            placeholder="Describe your ideal vacation..."
            rows="6"
            style="border: 1px solid #000; padding: 10px; border-radius: 5px;">
          </el-input>
        </el-form-item>
  
        <el-form-item>
          <el-button type="primary" @click="submitVacation">Submit</el-button>
        </el-form-item>
      </el-form>
    </div>
  </template>
  
  <script>
  export default {
    data() {
      return {
        vacationForm: {
          idealVacation: '',
        },
      };
    },
    methods: {
      submitVacation() {
        this.$axios.post('http://localhost:5000/saveVacation', this.vacationForm)
          .then(response => {
            console.log('Vacation info submitted', response);
            this.$router.push({ name: 'user-selection' });
          })
          .catch(error => {
            console.error('Error submitting vacation info', error);
          });
      },
    },
  };
  </script>
  
  
  <style>
  .border-box {
    border: 1px solid #000;
    padding: 20px;
    border-radius: 5px;
    width: 800px;
    height: 600px;
    margin: 120px auto;
    padding: 20px;
    box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.1);
  }
  
  .ideal-vacation {
    width: 800px;
    height: 550px;
    margin: 120px auto;
    padding: 20px;
    border: 1.5px solid #ccc;
    border-radius: 10px;
    background-color: #f9f9f9;
  }
  
  .form-title {
    font-family: 'Times New Roman', Arial, sans-serif;
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 50px;
    margin-top: 40px;
    text-align: center;
  }

  .el-textarea {
    margin-left: 90px;
    vertical-align: bottom;
    width: 600px;
    height: 300px;
    font-size: 18px;
  }

  .el-textarea__inner {

  }

  .el-button {
  font-size: 18px !important;
  font-weight: bold;
  background-color: #409eff;
  color: white;
  border-radius: 4px;
  margin-left: 350px !important;
}
  </style>
  