<template>
  <div class="user-signup">
    <div class="form-title">User Signup</div>

    <el-form ref="form" :model="form" label-width="120px">
      <el-form-item label="Name :">
        <el-input v-model="form.name"></el-input>
      </el-form-item>

      <el-form-item label="Age :">
        <el-input v-model="form.age" type="number"></el-input>
      </el-form-item>

      <el-form-item label="Gender :">
        <el-select v-model="form.gender" placeholder="Select Gender">
          <el-option label="Male" value="Male"></el-option>
          <el-option label="Female" value="Female"></el-option>
          <el-option label="Other" value="Other"></el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="Address :">
        <el-input v-model="form.address"></el-input>
      </el-form-item>

      <el-form-item label="Country :">
        <el-input v-model="form.country"></el-input>
      </el-form-item>

      <el-form-item label="Holiday Frequency per year :">
        <el-input v-model="form.holidayFrequency" type="number"></el-input>
      </el-form-item>

      <el-form-item label="Do you want to sign up for Gold Membership? :">
        <el-radio-group v-model="form.goldMembership">
          <el-radio label="Yes">Yes</el-radio>
          <el-radio label="No">No</el-radio>
        </el-radio-group>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="submitForm">Submit</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      form: {
        name: '',
        age: '',
        gender: '',
        address: '',
        country: '',
        holidayFrequency: '',
        goldMembership: '',
      },
    };
  },
  methods: {
    submitForm() {
      this.$axios.post('http://localhost:5000/signup', this.form)
      .then(response => {
          console.log('User info submitted', response);
          if (this.$route.path !== '/ideal-vacation') {
            this.$router.push('/ideal-vacation');
          }
        })
        .catch(error => {
          console.error('Error submitting form', error);
        });
    },
  },
};
</script>

